.popular-goods__wrapper {
  display: grid;
  grid-template-areas: 'title'
                        'slider'
                        'btn';

  @media #{$tablet-desktop} {
    grid-template-columns: 1fr auto;
    grid-template-areas: 'title btn'
                          'slider slider';
    margin-bottom: 40px;
  }
}

.popular-goods__title {
  grid-area: title;
  margin: 0 0 10px 0;
  letter-spacing: -0.03em;

  @media #{$tablet-desktop} {
    margin: 0 0 25px 0;
  }
}

.popular-goods-swiper {
  grid-area: slider;
  max-width: 100%;
}

.popular-goods__list {
  margin: 0 0 18px 0;
  padding: 10px 4px 23px 4px;
  list-style: none;

  @media #{$widemobile-desktop} {
    padding: 10px 4px 0 9px;
  }

  @media #{$tablet-desktop} {
    padding: 0;
  }

  @media #{$laptop-desktop} {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(289px, 1fr));
    row-gap: 20px;
    column-gap: 8px;
    margin: 0;
  }
}

.popular-goods__item {
  width: 343px;
  height: auto;
  margin: 0;

  @media #{$tablet-desktop} {
    width: 310px;
  }

  @media #{$laptop-desktop} {
    position: relative;
    width: 289px;

    &:hover {
      z-index: 2;
    }
  }
}

.popular-goods__arrows {
  grid-area: btn;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0 40px 0;

  @media #{$tablet-desktop} {
    margin: 0;
    align-self: flex-start;
  }

  @media #{$laptop-desktop} {
    display: none;
  }
}

.popular-goods__button {
  margin: 0 6px;

  @media #{$narrowlaptop-desktop} {
    width: 240px;
    margin: 0 auto;
  }

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.popular-goods {
  .good {
    padding: 8px 18px 18px 10px;
  }

  .good__main {
    padding: 0 4px 10px;
    row-gap: 12px;

    @media #{$laptop-desktop} {
      margin-top: 10px;
    }
  }

  .good__img-wrapper {
    height: 200px;

    @media #{$laptop-desktop} {
      height: 255px;
    }
  }

  .good__heading {
    @media #{$laptop-desktop} {
      font-size: 14px;
    }
  }

  .good__like {
    @media #{$laptop-desktop} {
      align-self: flex-start;
    }
  }

  .good__characteristics--value {
    margin: 0 8px 5px 0;
  }

  .good__labels-list {
    margin: 0 0 10px 7px;
  }

  .good__extra {
    padding: 0 4px;
  }

  .good__price-code {
    row-gap: 5px;
  }
}
