.image-row {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  overflow: hidden;
}

.image-row__item {
  margin: 0;
}

.image-row__link {
  display: block;
}

.image-row__img-wrapper {
  display: block;
  width: 120px;
  height: 120px;

  @media #{$tablet-desktop} {
    width: 214px;
    height: 214px;
  }
}

.image-row__img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.image-row--main-page {
  .image-row__img-wrapper {
    display: block;
    width: 300px;
    height: 265px;

    @media #{$tablet-desktop} {
      width: 526px;
      height: 500px;
    }
  }
}
